import Node from '../Components/Node';

export const makeMaze = (startLoc, endLoc, grid, chosenMap) => {
    chosenMap=setChosenMap(chosenMap);
    for (let i = 0; i < grid.length; i++) {
        for (let j = 0; j < grid[i].length; j++) {
            if ((i == startLoc[0] && j == startLoc[1]) || (i == endLoc[0] && j == endLoc[1])) continue;
            if (chosenMap[i][j] !== undefined && chosenMap[i][j] === 1) {
                grid[i][j] = <Node
                    isWall={true}
                    isStart={false}
                    isPath={false}
                    isVisited={false}
                    isEnd={false}
                />;
            }
        }
    }
    return grid;
}

const setChosenMap=(choice)=>{
    choice=choice.toLowerCase();
    if(choice==="loop"){
        return loopMap;
    }
    else if(choice==="maze 1"){
        return maze_1;
    }
    else if(choice==="maze 2"){
        return maze_2;
    }
    else if(choice==="boxed"){
        return boxes;
    }
}


